.hero{
    width:100%;
    height:100vh;
    /* margin-top:10px; */
    background:var(--bg-color);
    display: flex;
}
.hero-container{
justify-content: space-between;
}

.left-hero{
    width:45%;
    
}
.left-hero p{
    line-height:25px;    
}
.right-hero{
    width: 50%; 
    justify-content: center;
}

.right-hero img{
    width:300px;
    border: 2px solid brown;
    border-radius:50%;
    animation: imgMovement 4s linear infinite;
}

@keyframes imgMovement {
    10%{
        transform: translateX(50px);
        border: 2px solid green;
    }
    30%{
        transform: translateX(-10px);
        border: 2px dotted yellow;
    }
    70%{
        transform: translateX(80px);
        border: 2px dashed red;
    }
    100%{
        transform: translateX(10px);
        border: 2px solid blue;
    }
    
}

.left-hero h1{
     margin-bottom:1.2rem;   
}
.left-hero span{
    color:red;
    font-weight:700;
}








 

  @media screen and (max-width:768px){
    .hero{
        margin-top:30px;
    }
     
.hero-container{
    flex-direction: column;
}
    .left-hero{
        width:100%;
        padding:30% 0;
        text-align: center;
      }
      
      .right-hero{
        display:none;
      }

    }

  @media screen and (max-width:576px){

    .hero{
        margin-top:10px;
    }
  
   
  }
  


   @media screen and (max-width:450px){
    .left-hero{ 
        padding:40% 0;
      }

      h1{font-size: 1.8rem;}
       h2{font-size: 1.5rem;} 
      h3{font-size: 1.2rem;}

 
    
   }

   
   @media screen and (max-width:420px){
    h1{font-size: 1.6rem;}
    h2{font-size: 1.4rem;} 
   h3{font-size: 1.2rem;}


   }



